<template>
  <div class="login-container">
    <!-- el-form组件：elementUI插件里面的一个组件，经常展示表单元素  model：用于收集表单数据  rules：表单验证规则-->
    <el-form ref="loginForm" :model="loginForm" :rules="loginRules" class="login-form" auto-complete="on" label-position="left">

      <div class="title-container">
        <h3 class="title">登录</h3>
      </div>

      <el-form-item prop="username">
        <span class="svg-container">
         <svg-icon icon-class="user" />
        </span>
        <el-input
      
        class="username"
          ref="username"
          v-model="loginForm.username"
          placeholder="Username"
          name="username"
          type="text"
          tabindex="1"
          auto-complete="on"
        />
      </el-form-item>

      <el-form-item prop="password">
        <span class="svg-container">
          <svg-icon icon-class="password" />
        </span>
        <el-input
        class="password"
          :key="passwordType"
          ref="password"
          v-model="loginForm.password"
          :type="passwordType"
          placeholder="Password"
          name="password"
          tabindex="2"
          auto-complete="on"
          @keyup.enter.native="handleLogin"
        />
        <span class="show-pwd" @click="showPwd">
          <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
        </span>
      </el-form-item>

      <el-button :loading="loading" type="primary" style="width:30%;margin-bottom:30px;margin-left:36%;" @click.native.prevent="handleLogin">登录</el-button>

      <div class="tips">
        <span style="margin-left:35%;font-size:8px;color:white;">username: admin</span>
        <span  style="font-size:8px;color:white;"> password: any</span>
      </div>

    </el-form>
  </div>
</template>

<script>
import { validUsername } from '@/utils/validate'

export default {
  name: 'MyLogin',
  data() {
    //先不用在意：这里面在进行表单验证，验证用户名与密码操作
    //回首在看这里
    const validateUsername = (rule, value, callback) => {// eslint-disable-line no-unused-vars
      if (!validUsername(value)) {
        callback(new Error('Please enter the correct user name'))
      } else {
        callback()
      }
    }
    const validatePassword = (rule, value, callback) => {// eslint-disable-line no-unused-vars
      if (value.length < 6) {
        callback(new Error('The password can not be less than 6 digits'))
      } else {
        callback()
      }
    }
    return {
      loginForm: {
        username: 'admin',
        password: '111111'
      },
      loginRules: {
        // username: [{ required: true, trigger: 'blur', validator: validateUsername }],
        // password: [{ required: true, trigger: 'blur', validator: validatePassword }]
      },
      loading: false,
      passwordType: 'password',
      redirect: undefined
    }
  },
  watch: {
    $route: {
      handler: function(route) {
        this.redirect = route.query && route.query.redirect
      },
      immediate: true
    }
  },
  methods: {
    showPwd() {
      if (this.passwordType === 'password') {
        this.passwordType = ''
      } else {
        this.passwordType = 'password'
      }
      this.$nextTick(() => {
        this.$refs.password.focus()
      })
    },
    //登录业务：发请求，带着用户名与密码给服务器（成功与失败）
    handleLogin() {
      //这里是在验证表单元素（用户名与密码）的是否符合规则
      this.$refs.loginForm.validate(valid => {
        //如果符合验证规则
        if (valid) {
          //按钮会有一个loading效果
          this.loading = true;
          //派发一个action:user/login,带着用户名与密码的载荷
          this.$store.dispatch('user/login', this.loginForm).then(() => {
            //登录成功进行路由的跳转
            this.$router.push({ path: this.redirect || '/' });
            //loading效果结束
            this.loading = false
          }).catch(() => {
            this.loading = false
          })
        } else {
          console.log('error submit!!')
       
          return false
        }
      })
    }
  }
}
</script>


 <style scoped>
 *{
   padding: 0;
   margin: 0;
 }
 .login-container{
   background: url(@/picture/1.jpg);
   width: 100%;
   min-height: 100%;
   background-size: 100% 100%;
 }
 .login-form {
    position: relative;
    width: 520px;
    height: 400px;
    max-width: 100%;
    padding: 160px 35px 0;
    margin: 0 auto;
    overflow: hidden;
  }
 
 .title{
   color:white;
   text-align: center;
   margin-top: 100px;
   margin-bottom: 10px;

 }
 .username,.password{
   margin-left: 35%;
    margin-bottom: 10px;
    
 }
  .svg-container {
    padding: 6px 5px 6px 15px;
    color:gray;
    vertical-align: middle;
    width: 30px;
    display: inline-block;
   
  }
 


 </style>